.exp {
  position: relative;
  min-height: calc(100vmin - 6rem);
  height: max-content;
  width: 100%;
  padding: 4rem 10rem;
  margin: 0 auto;
  box-shadow: 0 10px 10px -4px rgba(0, 0, 0, 0.5);
}
.exp h1 {
  color: #ff7900;
  font-size: 5rem;
  margin-bottom: 1rem;
}
.exp .comin {
  width: max-content;
  margin: 0 auto;
}
.exp .comin img {
  width: 100%;
  height: 100%;
}
.down4 {
  position: absolute;
  bottom: 2rem;
  font-size: 7rem;
  font-weight: 800;
  left: 50%;
  color: #5dd3f9;
  transform: translateX(-50%);
  cursor: pointer;
}
@media only screen and (max-width: 1024px) {
  .exp {
    height: max-content;
  }
  .exp .comin {
    width: 95%;
  }
}
@media only screen and (max-width: 624px) {
  .exp {
    padding: 4rem 6rem;
    box-shadow: 0 10px 10px -4px rgba(0, 0, 0, 0.3);
  }
  .exp h1 {
    font-size: 3rem;
  }
  .down4 {
    font-size: 5rem;
  }
}
