.d-flex {
  display: flex;
  align-items: center;
  position: relative;
}
.d-flex input {
  height: 3rem;
  width: 3rem;
  border-radius: 1.5rem;
  outline: none;
  border: 1px solid #00c;
  margin: 0 1.5rem;
  background-color: transparent;
}
.d-flex input:focus {
  padding: 0 2.5rem 0 1rem;
  width: 15rem;
}
.d-flex .fa-magnifying-glass {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8.5rem;
  z-index: -1;
}
.d-flex .Nbtn {
  height: 3rem;
  outline: none;
  width: 6rem;
  border: none;
  background-color: #58008e;
  border-radius: 5px;
  padding: 0 1rem;
  font-size: 1rem;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  display: grid;
  place-content: center;
}
.container {
  width: 90%;
  margin: 0 auto;
  margin-top: 2rem;
}
.container .cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.container .cards .card {
  width: calc(90% / 4);
  margin: 1.5rem;
  position: relative;
  padding-bottom: 1.5rem;
}
.container .cards .card img {
  width: 100%;
  height: 18rem;
}
.container .cards .card .body {
  font-family: "Inter", sans-serif;
}
.container .cards .card .body .title {
  font-size: 1.5rem;
  margin: 0.7rem 0 0.5rem 0;
}
.container .cards .card .body .subtitle {
  font-size: 1rem;
  float: right;
  color: grey;
}
.container .cards .card .body .text {
  font-size: 1.5rem;
}
.container .cards .card .body .card-link {
  position: absolute;
  color: #ff7900;
  text-decoration: none;
  bottom: 0;
}
@media only screen and (max-width: 1024px) {
  .container .cards .card {
    width: calc(90% / 3);
  }
  .container .cards .card img {
    height: 15rem;
  }
  .container .cards .card .title {
    font-size: 1.7rem !important;
  }
}

@media only screen and (max-width: 804px) {
  .container {
    width: 100%;
  }
  .container .cards .card {
    width: calc(80% / 2);
  }
  .container .cards .card img {
    height: 15rem;
  }
  .container .cards .card .title {
    font-size: 1.7rem !important;
  }
}
@media only screen and (max-width: 624px) {
  .container .cards .card {
    width: 80%;
  }
}
