.navbar {
  position: sticky;
  top: 0;
  height: 6rem;
  width: 100%;
  padding: 0 6rem;
  background: #ffffff;
  box-shadow: 0px 10px 1.2rem -1rem rgba(0, 0, 0, 0.5);
  transition: all 0.3s linear;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}
.navbar .logo {
  position: relative;
  height: 100%;
  cursor: pointer;
}
.navbar .logo .logoImg {
  height: 100%;
}
.navItems {
  padding-right: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.navItem {
  color: black;
  text-decoration: none;
  padding: 0 1rem;
  margin: 0 0.5rem;
  font-family: "Inter", sans-serif;
  font-size: 1.7rem;
  line-height: 2.4rem;
  text-align: center;
  cursor: pointer;
}
.navItem:hover,
.navItem.active {
  color: #00c2ff;
  text-decoration: underline;
}
@media only screen and (max-width: 624px) {
  .navbar {
    padding: 0 1.6rem;
  }
  .navbar .logo {
    height: 80%;
  }
  .navItems {
    padding-right: 0.5rem;
  }
  .navItem {
    padding: 0 0.5rem;
    margin: 0 0.5rem;
    font-size: 1.5rem;
  }
}
@media only screen and (max-width: 400px) {
  .navbar {
    padding: 0 1rem;
  }
  .navbar .logo {
    height: 60%;
  }
  .navItems {
    padding-right: 0.2rem;
  }
  .navItem {
    padding: 0 0.3rem;
    margin: 0 0.4rem;
    font-size: 1.3rem;
  }
}
